.signin{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffd770;
}
.signin > .signin__wrapper{
  width: 50vh;
  height: 50vh;
  background-color: #7691d9;
  border: none;
  box-shadow: 0 10px 30px rgb(59, 5, 5);
}
.signin > .signin__wrapper > .signin__form{
  height: 80%;
}
.signin > .signin__wrapper > .signin__form > form{
  padding-top: 40px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
} 
.signin > .signin__wrapper > .signin__form > form > input,
.signin > .signin__wrapper > .signin__form > form > button{
  width: 80%;
  height: 40px;
  border: none;
}
.signin > .signin__wrapper > .signin__form > form > h2{
  font-size: 30px;
  font-weight: 600;
  padding-left: 10px;
  padding-bottom: 40px;
  color: white;
}
.signin > .signin__wrapper > .signin__form > form > input{
  margin-bottom: 15px;
  font-size: 22px;
  padding-left: 10px;
  background-color: white;
}
.signin > .signin__wrapper > .signin__form > form > button{
  font-size: 22px;
  font-weight: 600;
  color: rgb(17, 17, 17);
  background-color: #a9f0ea;
}

.signin > .signin__wrapper > .signin__register{
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
}
.signin > .signin__wrapper > .signin__register > p{
  color: white;
  font-weight: 500;
}
.signin > .signin__wrapper > .signin__register > p > .signin__register__create{
  cursor: pointer;
  color: #ffecb0;
  font-weight: 800;
}
.signin > .signin__wrapper > .signin__register > p > .signin__register__create:hover{
  color: #3d3a3a;
}