.wrapper {
  display: flex;
  position: absolute;
  right: 25px;
  margin-top: -850px;
}

input {
  height: 30px;
  width: 225px;
  border: none;
  border-bottom: 1 solid black;
  padding:  0 0px;
  outline: none;
}
.enter {
  width: 50px;
  height: 30px;
  font-size: 12px;
  background: none;
  border:  1px solid #84a0e0;
  border-radius: 5px;
  color: #84a0e0;
  margin-left: 10px;
}
.enter:hover{
  background: white;
  color: black;
  cursor: pointer;
}
.todo__item{
  position: absolute;
  cursor: move;
  min-width: 160px;
  color: black;
  padding: 1em;
  text-align: center;
  font-weight: 600;
  border-radius: 5px;
}
.delete {
  position: absolute;
  right: 5px;
  top: 5px;
  font-weight: 600;
  background: none;
  border: none;
}
.delete:hover {
  color: red;
  cursor: pointer;
}