*{
  margin: 0;
}
main{
  margin-left: 21%;
  background-color: #f2f6ff;
}
.calendar-layout{
  width: 100%;
  height: 100vh;
  display: flex;
}

.container{
  display: flex;
  flex-direction: column;
  height: 100%;
}
.header-container{
  flex-basis: 50px;
  background-color: white;
  display: flex;
  justify-content: space-between;
}
.calendar-container{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

/* Calendarheader */
.header-container > h2{
  font-size: 25px;
  margin-left: 10px;
  margin-top: 12px;
  font-weight: 600;
  color: #84a0e0;
}
.header-container > h3{
  font-size: 19px;
  margin-top: 20px;
  margin-bottom: 20px;
  font-weight: 600;
  color: #84a0e0;
}
.move-button{
  width: 30px;
  height: 30px;
  display: inline-block;
  margin : 8px 9px;
}
.icon{
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
}
.left-img{
  background-image: url('../icon/left-icon.png');
}
.right-img{
  background-image: url('../icon/right-icon.png');
}
.header-middle{
  line-height: 50px;
}
.header-calendarYM{
  box-sizing: border-box;
  padding-left: 5px;
  flex-grow: 1;
}
.header-buttons{
  flex-basis: 120px;
  padding-left: 0;
  padding-top: 4px;
  display: block;
  list-style-type: none;
  margin: 0;
}
.header-buttons li{
  float: left;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
}

/* Calendar */
.calendar-date-header{
  display: flex;
  height: 50px;
}
.calendar-date-component{
  width: 170px;
  text-align: center;
  box-sizing: border-box;
  background-color: white;
  border-bottom: 0.1px solid #84a0e0;;
  font-size: 1.5rem;
  line-height: 50px;
}
.calendar-date-component:nth-child(odd){
  border-left: none;
  border-right: none;
}
.date-weekday{
  color:black;
  font-weight: 600;
}
.date-sun{
  color: tomato;
  font-weight: 600;
}
.date-sat{
  color: cornflowerblue;
  font-weight: 600;
}

.calendar-day-container{
  width: 170px;
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
}
.calendar-day{
  box-sizing: border-box;
  position: relative;
  flex-basis: 170px;
  font-weight: 600;
  border-top: none;
  background-color: white;
}

.calendar-week{
  display: flex;
  flex-grow: 1;
}
.calendar-day:nth-child(odd){
  border-left : none;
  border-right: none;
}
.calendar-day-label{
  display: inline-block;
  font-size: 25px;
  position: absolute;
  text-align: center;
  line-height: 25px;
  padding-top: 21px;
  width: 100px;
  height: 50px;
  right: 30px;
  top: 30px; 
}
.date-weekday-label{
  color : black;
}

.selected label{
  color: white;
  width: 70px;
  height: 70px;
  background-color: #ffbfd3;
  border-radius: 100%;
  font-size: 25px;
  right: 46px;
  top: 30px; 
}