header{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #7691d9;
  width: 21%;
  height: 100%;
  padding: 10vh 0 72px 40px !important;
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  color: white;
}
header > h1{
  width: 100%;
  letter-spacing: 7px;
}
header > h1 > .username{
  font-size: 70px;
  font-weight: 600;
}
header > h1 > .note{
  font-size: 66px;
  color: #ffecb0;
  font-weight: 600;
}
.bottom > h3, .bottom > p{
  width: 91%;
  line-height: 1.2;
  text-align: justify;
}
.bottom > h3{
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 30px;
  border-bottom: 3px solid #ffecb0;
}
.bottom > p{
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 40px;
  color: white;
}
.bottom > nav > ul{
  width: 80%;
  display: flex;
  flex-wrap: wrap;
}
.bottom  nav > ul > li{
  cursor: pointer;
  margin-right: 11px;
  font-size: 13px;
  color: white;
  opacity: 0.5;
  border-bottom: 1px solid #ffecb0;
  margin-bottom: 20px;
}
.bottom > nav > ul > li:hover{
  opacity: 1;
}